@charset "utf-8";

@import 'modules/reset';
@import 'mixins/index';
@import 'var/index';
@import 'base/base';
@import 'modules/index';

.kof {
    font-size: 12px;
}

.number {
    font-size: 64px;
    font-weight: bold;

    &--1 {
        color: green;
    }

    &--2 {
        color: orange;
    }
}

.effect {
    color: rgba(54, 162, 235, 1);
}

.noeffect {
    color: tomato;
}

.choices__inner {
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.125);
    border-radius: 4px;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important;
}

th {
    font-weight: bold;
}

.form-signin {
    width: 100%;
    max-width: 420px;
    padding: 15px;
    margin: auto;
}

.settings {
    position: absolute;
    cursor: pointer;
    right: 5px;
    top: 5px;

    &:hover {
        svg {
            fill: rgba(#000, 0.8);
        }
    }
    svg {
        width: 20px;
        height: 20px;
        fill: rgba(#000, 0.5);
        transition: all 250ms;
    }
}
